body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header{
  padding:20px 70px;
  background-color: black;
  color:azure;
  font-weight: bold;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  letter-spacing: 5px;
  display:flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position:relative;
  right:7px;
  top:-6px;
}
.cart-icon{
  display: flex;
}
.icon{
  font-size: 30px;
}
img{
  height: 150px;
}
.cart-wrapper{
  display:inline-flex;
  
  width:320px;
  box-shadow: 0px 0px 7px black;
  border-radius: 5px;
  gap:10px;
}
.image-wrapper{
  width:180px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border-right: 1px solid black;
}
.content{
  display: flex;
  flex-direction: column;
  flex-grow:1;
  gap:10px;
  padding:10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color:white;
  border-radius: 3px;
}
.quantity{
  display:flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid grey;
}
.quantity div{
  height: 22px;
  width:22px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quantity >div:first-child,
.quantity >button{
  background-color: rgb(193, 67, 115) !important;
  color:white;
  border-radius:0 ;
}
.quantity>button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 70vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap: 50px;
}
.total{
  position: fixed;
  background-color: gray;
  padding: 20px;
  padding-right: 30px;
  bottom: 0;
  width: 100%;
  text-align: right;
  box-sizing: border-box;
}
